<template>
  <ai-capsules v-model="select" size="s" :list="list" @change="changeHandler"></ai-capsules>
</template>
<script>
export default {
  data() {
    return {
      list: [
          { text: 'IOS', value: 1 },
          { text: 'ANDROID', value: 2 },
          { text: 'BLACKBERRY', value: 3 },
      ],
      select: { text: 'ANDROID', value: 2 },
      changeVal: '',
    }
  },
  methods: {
    changeHandler (val) {
      this.changeVal = JSON.stringify(val)
    }
  }
}
</script>
